import React from 'react';
import styles from './covidtracker.module.css';
import World from './World/world';
import India from './India/india';
import States from './state/state';
import Stategraph from './state/stategraph';
import {getworlddata,getcountrydata} from './api';
import GoogleAd from './GoogleAd'
class Covidtracker extends React.Component {
constructor(props) {
    super(props);

    this.state = {
        worlddata:{},
        countrydata:{},
        statedata:{},
        statenames:{},
        stateselect:'',
        selecteddata:{}
    };
}
async componentDidMount(){

    const k= await getworlddata();
    const cdata=await getcountrydata();
    
    let confirmed=0,recovered=0,deceased=0,active=0;
    for(let i in cdata){
        if (cdata[i].districts){
            confirmed+=cdata[i].total.confirmed
            recovered+=cdata[i].total.recovered
            if (cdata[i].total.deceased === undefined) {
            }
            else{
                deceased+=cdata[i].total.deceased;
            }
          
        }
       
        
    }
    active=confirmed-recovered-deceased;
    let cnew={confirmed:confirmed,active:active,recovered:recovered,deaths:deceased}
    
    // // const q= await getcountrydata();
    // // const s= await getstatedata();
    // let statename=[];
    // for(let i=0;i<s.length;i++){
    //     statename.push(s[i].state);

    // }
    // this.setState({statenames:statename});
    // this.setState({statedata:s});
    
     this.setState({worlddata:k});
     this.setState({statedata:cdata});

     this.setState({countrydata:cnew});
}
    
stateselector= async (state)=>{
   if (state==="select"){
       this.setState({stateselect:""});
   } 
   else{
       let c=state;
    let k=this.state.statedata[state];
   
    const selectdata={
    confirmed:k.total.confirmed,
    active:k.total.confirmed-k.total.recovered-k.total.deceased,
    recovered:k.total.recovered,
    deaths:k.total.deceased,
    name:c
   
    
    
    
    }
    
    
    
    
    
    this.setState({
        selecteddata:selectdata,
        stateselect:c
    
    
    
    })
    
    
    


   }

}

    render() {
        return (<div className={styles.container}>
            
                <GoogleAd slot="989038934" classNames="page-top"/>
                <World  worlddata={this.state.worlddata}/>
                 <India countrydata={this.state.countrydata}/>
                 <GoogleAd slot="394738798" timeout={1000} classNames="page-bottom" />
                 <States selector={(state)=>this.stateselector(state)}/>  
         <div>{
            !(this.state.stateselect==='')?(<Stategraph data={this.state.selecteddata} stateselected={this.state.stateselect} />):null
            
            }</div>


        </div>);
    }
}

export default Covidtracker;